import {
	mapGetters,
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			initStatus:false,//启动页是否存在
			// 轮播相关
			initSwiper:false,
			indicatorDots:true,
			autoplay:true,
			interval: 2000,
			duration: 500
		}
	},
	computed: {
		...mapGetters([
			'getMicList', //歌曲列表
			'getMicInfo', //歌曲详细信息
			'getMicStatus' //是否在播放
		]),

	},

	onShow() {
		const that = this;
		var obj = {
			all: "修改了",
			info: "我被修改了",
			name: "我被修改了"
		}
	
		this.SET_MICINFO(obj)
		
	},
	methods: {
		...mapMutations([
			"SET_MICLIST",
			"SET_MICINFO",
			"SET_MICSTATUS"
		]),
		// 关闭启动页
		close(){
			
			uni.switchTab({
				url:"/pages/index/index"
			})
		},
		// 启动页轮播
		swiper_change(e){
			let that = this;
			console.log(e.detail)
			if(e.detail.current == 2){
				setTimeout(function() {
					that.close()
				}, 1000);
			}
		},
		go_details() {
			uni.navigateTo({
				url: "/pages/micPlayPage/micPlayPage"
			})
		},
		getScancode: function() {
			var _this = this;
			// 允许从相机和相册扫码aa
			wx.scanCode({
				success: (res) => {
					_this.result = res.result
				}
			})

		},
		go_jd() {
			wx.navigateToMiniProgram({
				appId: 'wx91d27dbf599dff74',
				path: 'pages/item_wqvue/detail/detail?__pid=Pxc83sui8udq',
				extraData: {
					foo: 'bar'
				},
				envVersion: 'develop',
				success(res) {
					console.log(res);
				}
			})
		}
	}
}
